@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap");
:root {
  font-family: Nunito, Arial, Helvetica, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

h2 {
  font-size: 1.75rem;
}

h3 {
  font-size: 1.5rem;
}

button {
  font-weight: bold;
  font-size: 1rem;
  padding: 4px 12px;
  border: 2px solid #ededed;
  border-radius: 1rem;
  background-color: #2f465c;
  color: #fff;
  cursor: pointer;
}

button:hover {
  outline: 2px solid #2f465c;
}

button.danger {
  background-color: #760505;
}

#body {
  display: grid;
  grid-template-rows: 75px calc(100vh - 125px) 50px;
}
@media screen and (max-width: 800px) {
  #body {
    grid-template-columns: 100vw;
    grid-template-rows: 75px 1fr auto;
    overflow-x: hidden;
  }
}
@media screen and (max-width: 1200px) {
  #body {
    grid-template-rows: 75px 1fr auto;
  }
}
#body main {
  max-width: 1200px;
  padding: 32px;
  margin: auto;
  display: grid;
  gap: 16px;
  grid-template-columns: 1fr 3fr;
}
@media screen and (max-width: 1200px) {
  #body main {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 800px) {
  #body main {
    grid-template-columns: 100vw;
    padding: 0;
    gap: 0;
  }
}
#body main .experiences-group {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}
@media screen and (max-width: 800px) {
  #body main .experiences-group {
    margin: 32px;
    grid-template-columns: 1fr;
  }
}


