@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap");
:root {
  font-family: Nunito, Arial, Helvetica, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

h2 {
  font-size: 1.75rem;
}

h3 {
  font-size: 1.5rem;
}

button {
  font-weight: bold;
  font-size: 1rem;
  padding: 4px 12px;
  border: 2px solid #ededed;
  border-radius: 1rem;
  background-color: #2f465c;
  color: #fff;
  cursor: pointer;
}

button:hover {
  outline: 2px solid #2f465c;
}

button.danger {
  background-color: #760505;
}

.exp-item-display {
  display: flex;
  flex-flow: column;
  gap: 4px;
}
.exp-item-display .exp-buttons {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}


